:root {
    --bg-color: rgba(0,0,0,0.5);
    --bg-color-hover: rgba(0,0,0,0.75);
    --font-color: #FDE5D4;
}

* {
    margin: 0;
    box-sizing: border-box;
}

.nav-big{
    position: fixed;
    top: 0;
    z-index: 500;
    width: 100%;
    height: 60px;
    background-color: rgba(0,0,0,0.85);
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.nav-big ul{
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.nav-big ul li{
    list-style: none;
    border-radius: 15px;
}
.nav-big ul li a {
    margin-left: 20px;
    margin-right: 20px;
    font-size: 30px;
    list-style: none;
    text-decoration: none;
    color: aliceblue;
    transition: transform 0.234s;
}
.nav-big ul li:hover {
    background-color: var(--bg-color-hover);
    transform: scale(1.15678);
}
.nav-logo{
    height: 58px;
    transition: transform 0.234s;
    border-radius: 15px;
}
.nav-logo:hover{
    transform: scale(1.15678); 
    background-color: var(--bg-color-hover);
}
.nav-name {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 45px;
    width: 30%;
    font-family: Times New Roman,Times,serif;
    transition: transform 0.234s;
    cursor: pointer;
    color: aliceblue;
    border-radius: 15px;
}
.nav-name:hover{
    transform: scale(1.15678); 
    background-color: var(--bg-color-hover);
}
.git-logo{
    height: 40px;
    transition: transform 0.234s;
    border-radius: 15px;
}
.git-logo:hover{
    transform: scale(1.15678); 
    background-color: var(--bg-color-hover);
}
.linkedin-logo{
    height: 40px;
    transition: transform 0.234s;
    border-radius: 15px;
}
.linkedin-logo:hover{
    transform: scale(1.15678); 
}
/* ---------------- Navbar and burger menu ------------------ */
nav {
    width: 100%;
    height: 5em;
    background-color: rgb(0 0 0);
    display: none;
    justify-content: space-around;
    align-items: center;
    padding: 10px;
}

.burger-menu {
    height: 45px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    cursor: pointer;
}

.burger-bar {
    width: 50px;
    height: 6px;
    background-color: rgb(0, 183, 255);
    border-radius: 6px;
}



.menu {
    width: 70%;
    height: 100%;
    background-color: black;
    opacity: 0.9;
    position: absolute;
    right: 0;
    top: 80px;
    z-index: 0;
    animation: slide_left 0.5s;
}
.side{
    padding: 0px;
    display: flex;
    flex-direction: column;
    height: 30%;
    align-items: center;
}
.side li {
    margin: 10px;
    list-style: none;
    font-size: 30px;
}
.side li a{
    text-decoration: none;
    color: antiquewhite;
}
.media {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 15px;
}
/* ------------- sliding menu ------------------ */
.hidden {
    display: none;
}

.visible {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

/* ----- animations--------- */
/* clicked */
.burger-bar.clicked:nth-child(1){
    transform: rotate(45deg) translate(0.75em, 1.25em);
    transition: ease-out 0.4s;
}

.burger-bar.clicked:nth-child(2){
    transform: scale(0.1);
    transition: ease-out 0.4s;
}

.burger-bar.clicked:nth-child(3){
    transform: rotate(135deg) translate(-0.5em, 1em);
    transition: ease-out 0.4s;
}

/* unclicked */
.burger-bar.unclicked {
    transform: rotate(0) translate(0);
    transition: cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.5s;
}





@media (max-width:700px){
    .nav-big{
        display: none;
    }
    nav{
        display: flex;
    }
    
}



@keyframes slide_left {
    from {
        /* opacity: 0; */
        transform: translateX(+100%);
    }
    to {
        /* opacity: 1; */
        transform: translateX(0);
    }
  }