


.project{
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    border-radius: 15px;
    margin: 0px 25px 25px 25px;
    transition: transform 0.234s;
}
.project:hover{
    transform: scale(1.025);
}
.project-pic{
    display: flex;
    align-items: center;
    justify-content: center;
    width:60%
}
.project-info{
    margin: 15px;
    font-size: 30px;
    color: azure;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 40%;
}
.project-info a{
    text-decoration: none;
    color: rgb(0, 0, 0);
    padding: 10px;
    background-color: beige;
    margin: 10px;
    border-radius: 15px;
    transition: transform 0.234s;
}
.project-info p{
    color: aqua;
    display: flex;
    align-items: center;
    justify-content: center;
}
.project-info a:hover{
    transform: scale(1.025);
}
.project-pic img{
    margin: 15px;
    border-radius: 15px;
    display: flex;
    align-items: center;
    width:90%
}




@media (max-width:700px){
    .project{
        padding: 20px;
        border: solid aquamarine;
        display: flex;
        flex-direction: column;
    }
    .project-pic{
        width:95%
    }
    .project-info{
        width: 80%;
        font-size: 25px;
    }
}