.contact{
    background-color: rgba(0, 0, 0, 0.5);
    color: aliceblue;
    display: flex;
    justify-content: center;
    align-items: center;
    align-items: center;
    border-radius: 15px;
    margin: 0px 25px 25px 25px;
    transition: transform 0.234s;
    width: 95%;
    height: 50px;
}
.contact:hover{
    transform: scale(1.025);
}
.contact a{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 5px;
    color: aliceblue;
    text-decoration: none;
}